<template>
	<div id='table'>
		<table>
			<tr>
				<th class="area">地区</th>
				<th class="nowValue">现存确诊</th>
				<th class="value">累计确诊</th>
				<th class="deathNum">死亡</th>
				<th class="cureNum">治愈</th>
			</tr>
			<tr :key="index" v-for="(item,index) in message">
				<td class="area">{{message[index].name}}</td>
				<td>{{message[index].value-message[index].deathNum-message[index].cureNum}}</td>
				<td>{{message[index].value}}</td>
				<td>{{message[index].deathNum}}</td>
				<td>{{message[index].cureNum}}</td>
			</tr>
		</table>
	</div>
</template>

<script>
import jsonp from 'jsonp'

export default {
  name: 'Table',
	data(){
		return{
			message: {
			}
		}
	},
	created(){
		this.getData();
	},
	methods:{
		getData(){
			jsonp('http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',{},(err,data)=>{
				if(!err){
					// console.log(data);
					let list = data.data.list.map(item => ({
						name:item.name,
						value:item.value,
						deathNum:item.deathNum,
						cureNum:item.cureNum
					}))
					// console.log(list);
					this.message = list;
				}
			})
		}
	}
}
</script>

<style>
table{
	display: block;
	margin: 20px 0;
	padding-left: 30%;
	background-color: #f7f7f7;
	font-family: Microsoft Yahei;
}
th{
	width: 102.4px;
	height: 40.96px;
	font-size: 15.36px;
	background-color: #e3e7f3;
	border-right: 1px solid #fff;
}
.area{
	width: 150px;
	text-align: left;
	padding-left: 20px;
}
.nowValue{
	background-color: #f3bab0;
}
.value{
	background-color: #e69a8d;
}
.deathNum{
	background-color: #b4c0d5;
}
.cureNum{
	background-color: #95db9a;
}
td{
	height: 40.95px;
	color: #333;
	font-weight: 700;
	font-size: 19.2px;
	text-align: center;
	border-right: 2px solid #fff;
}

</style>
