<template>
	<div id="compare">
		<ul>
			<li>
				<b>较昨日<em style="color: rgb(247, 76, 49);">{{message.addgn}}</em></b>
				<strong style="color: rgb(247, 76, 49);">{{message.gntotal}}</strong>
				<span>累计确诊</span>
			</li>
			<li>
				<b>较昨日<em style="color: rgb(247, 130, 7);">{{message.addsus}}</em></b>
				<strong style="color: rgb(247, 130, 7);">{{message.sustotal}}</strong>
				<span>现存疑似</span>
			</li>
			<li>
				<b>较昨日<em style="color: rgb(93, 112, 146);">{{message.adddeath}}</em></b>
				<strong style="color: rgb(93, 112, 146);">{{message.deathtotal}}</strong>
				<span>累计死亡</span>
			</li>
			<li>
				<b>较昨日<em style="rgb(40, 183, 163);">{{message.addcure}}</em></b>
				<strong style="color: rgb(40, 183, 163);">{{message.curetotal}}</strong>
				<span>累计治愈</span>
			</li>
		</ul>
	</div>
</template>

<script>
import jsonp from 'jsonp'

export default {
  name: 'Compare',
	data(){
		return{
			message: {
			}
		}
	},
	created(){
		this.getData();
	},
	methods:{
		getData(){
			jsonp('http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',{},(err,data)=>{
				if(!err){
					// console.log(data);
					let list = {
						addgn:-(parseInt(data.data.gntotal)-parseInt(data.data.historylist[1].cn_conNum)),
						addsus:-(parseInt(data.data.sustotal)-parseInt(data.data.historylist[1].cn_susNum)),
						adddeath:-(parseInt(data.data.deathtotal)-parseInt(data.data.historylist[1].cn_deathNum)),
						addcure:-(parseInt(data.data.curetotal)-parseInt(data.data.historylist[1].cn_cureNum)),
						gntotal:data.data.gntotal,
						sustotal:data.data.sustotal,
						deathtotal:data.data.deathtotal,
						curetotal:data.data.curetotal,
					}
					// console.log(list);
					this.message = list;
				}
			})
		}
	}
}
</script>

<style>
#compare{
	font-family: "microsoft yahei";
}
ul{
	display: flex;
	list-style: none;
	margin: 10px 0;
	padding: 0;
}
li{
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 25%;
}
b{
	font-size: 14px;
	color: #858585;
}
span{
	font-size: 16px;
	font-weight: 700;
}
</style>
