<template>
  <div class="map" ref='mapbox'></div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/china.js'
import jsonp from 'jsonp'

const option = {
  title: {
		text: '臭屁屁疫情地图',
		textStyle: {
			color: 'red',
		},
		subtext: '大臭屁永远喜欢小臭屁',
		subtextStyle: {
			color: 'pink'
		},
	},
	series: {
		name: '现存确诊人数',
		type: 'map',
		map: 'china',
		roam: 'true',
		zoom: '1.2',
		scaleLimit: {
			min: '1',
			max: '2'
		},
		label: {
			show: true,
			color: '#000',
			fontSize: 10,
			fontWeight: 400
		},
		itemStyle: {
			// areaColor: 'skyblue',
		},
		emphasis: {
			label: {
				color: '#fff',
				fontSize: 20
			},
			itemStyle: {
				areaColor: '#ffbe00',
			}
		},
		data: []
	},
	visualMap: {
		type: 'piecewise',
		show: 'true',
		pieces: [
			{min: 10000},
			{min: 1000,max: 10000},
			{min: 100,max: 1000},
			{min: 10,max: 100},
			{min: 1,max: 10},
			{value: 0,color: 'rgba(253,235,207,1)'}
		],
		inRange: {
			color: ['#fdebcf','#f59e83','#e55a4e','#cb2a2f','#811c24']
		}
	},
	tooltip: {
		trigger: 'item',
		formatter: '省份：{b0}<br/>现存确诊人数：{c0}'
	}
}

export default {
  name: 'Map',
	mounted(){
		this.getData();
		this.mychart = echarts.init(this.$refs.mapbox);
		this.mychart.setOption(option)
	},
	methods:{
		getData(){
			jsonp('http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427',{},(err,data)=>{
				if(!err){
					console.log(data);
					let list = data.data.list.map(item => ({
						name:item.name,
						value:parseInt(item.value)-parseInt(item.deathNum)-parseInt(item.cureNum)}))
					// console.log(list)
					option.series.data = list;
					this.mychart.setOption(option);
				}
			})
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.map{
	width: 1000px;
	height: 700px;
	margin: 0 auto;	
}
@media screen and (min-width: 320px) and (max-width: 500px){
	.map{
		width: 400px;
		height: 400px;
		margin: 0 auto;
	}	
}
</style>
